import * as React from 'react';
import * as glamorous from 'glamorous';
import {
  logoCardWrapper,
  logoCardImage,
  logoWrapper,
  verticalHelper,
} from './LogoGarden-styles';

const { Component, PropTypes } = React;
const {
  Div,
  A,
  Img,
  Span,
} = glamorous;

export interface ILogoCardProps {
  title: string;
  image: string;
  imageAlt?: string;
  image2x?: string;
  link: string;
}

export class LogoCard extends Component<ILogoCardProps, void> {
  static propTypes = {
    title: PropTypes.string,
    image: PropTypes.string,
    image2x: PropTypes.string,
    link: PropTypes.string,
  };
  render() {
    const {
      title,
      image,
      image2x,
      link,
      imageAlt,
    } = this.props;
    const isRetina = window.devicePixelRatio >= 1.5;
    return (
      <Div css={logoCardWrapper()}>
        <A href={link} target="_blank" title={title}>
          <Div css={logoWrapper(isRetina)}>
            <Span css={verticalHelper()}/>
            <Img src={(isRetina && image2x) ? image2x : image} alt={imageAlt} css={logoCardImage()}/>
          </Div>
        </A>
      </Div>
    );
  }
}



