import * as React from 'react';
import glamorous from 'glamorous';
import { autobind } from 'core-decorators';
import * as _ from 'lodash';

import { LazyloadFade } from '../../../atoms/LazyloadFade';
import { Media } from '../../../molecules/Media';
import { IVideoProps } from '../SuccessStoryAdProduct';
import * as styles from './MobileVideo-styles';

const { Div } = glamorous;

export interface IMobileVideoProps {
  video: IVideoProps;
  isFocused?: boolean;
}

export interface IDeviceMobileState {
  playing: boolean;
  shouldShowPoster: boolean;
  initialPlay: boolean;
}

export class MobileVideo extends React.Component<IMobileVideoProps, IDeviceMobileState> {
  player: any;
  container: any;
  videoContainer: any;
  videoTop: number;
  scrollThrottle: any;

  static defaultProps = {
    isFocused: true,
  };

  state = {
    playing: false,
    shouldShowPoster: true,
    initialPlay: false,
  };

  componentDidMount() {
    // window.setTimeout(() => {
    //   this.videoTop = this.container.getBoundingClientRect().top;
    //   this.scrollThrottle = _.throttle(this.scrollCallback.bind(this), 100);
    //   window.addEventListener('scroll', this.scrollThrottle);
    //   this.scrollCallback();
    // }, 100);
  }

  componentWillReceiveProps(nextProps) {
    // Stop media when it looses focus, particularly for carousels.
    if ( nextProps.isFocused !== this.props.isFocused && !nextProps.isFocused ) {
      this.setState({
        playing: false,
        initialPlay: false,
      });
    }
  }

  // componentWillUnmount() {
  //   window.removeEventListener('scroll', this.scrollCallback);
  // }

  // @autobind
  // scrollCallback() {
  //   const scrollTop = Math.max(document.documentElement.scrollTop, document.body.scrollTop) + window.innerHeight - 60;
  //   if (this.videoContainer && (scrollTop - window.innerHeight) > (this.videoTop + this.videoContainer.offsetHeight)) {
  //     if (this.state.playing && this.player) {
  //       this.player.player.pause();
  //     }
  //   } else if (this.container && scrollTop >= this.videoTop) {
  //     if (this.state.playing && this.player) {
  //       this.player.player.play();
  //     }
  //   } else {
  //     if (this.state.playing && this.player) {
  //       this.player.player.pause();
  //     }
  //   }
  // }

  render() {
    const {
      playing,
      shouldShowPoster,
      initialPlay,
    } = this.state;

    return (
      <Div
        innerRef={(container: any) => { this.container = container; }}
        css={ styles.container(this.props.isFocused) }
      >
        <LazyloadFade>
          <Media
            ref={(player: any) => { this.player = player; }}
            isFocused={this.props.isFocused}
            ratioWidth={9}
            ratioHeight={16}
            fullWidth
            roundedCorner
            controls
            mediaWidthRelativeToContainer={'100%'}
            video={{
              videoSrc: this.props.video.src,
              posterImageUrl: this.props.video.posterUrl,
            }}
          />
        </LazyloadFade>
      </Div>
    );
  }
}



