import * as React from 'react';

import { css } from 'glamor';
import glamorous from 'glamorous';
const { Div } = glamorous;

const ReactPlayer = require('react-player');
import * as styles from '../Video-styles';
import { IVideoProps } from '../Video';
import { TweenMax } from 'gsap';
import { autobind } from 'core-decorators';

interface IVideoInnerProps extends IVideoProps {
  width: any;
  height?: any;
  playing: boolean;
  handlePosterTap: React.EventHandler<React.MouseEvent<HTMLDivElement>>;
  handleVideoEnded: Function;
  handleVideoPause: Function;
  isMobile: boolean;
  isVertical?: boolean;
  ctaText?: string;
  showCTA: boolean;
  smallLayout?: boolean;
  heroMediaStyle?: boolean;
  animOnScroll?: boolean;
  dragging: boolean;
  handlePlayback?: Function;
  onDuration?: Function;
  roundedCorner?: boolean;
  seekTo?: number;
  controls?: boolean;
  onKeyUp?: Function;
  fitHeight?: any;
  noShadow?: boolean;
  loop?: boolean;
  autosize?: boolean;
}

interface IVideoState {
  transitioningPoster: boolean;
  isReady: boolean;
  width: number;
  height: number;
}

export class VideoPlayer extends React.Component<IVideoInnerProps, IVideoState> {

  posterElement: HTMLElement;
  player: any;

  state = {
    transitioningPoster: false,
    isReady: false,
    width: 160,
    height: 90,
  };

  componentDidMount() {
    window.addEventListener('resize', this.resize);
    if (this.props.playing && this.player) {
       this.player.player.play();
    }

    // Required to get iPad to load poster image.
    const deviceOSList = ['iPhone', 'iPad'];
    const userAgent = navigator.userAgent;

    const deviceMatch = deviceOSList.filter((OS) => {
      return userAgent.indexOf(OS) > -1;
    });

    if (deviceMatch.length) {
      this.player.player.play();
    }

  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resize);
  }

  buffering(e) {
    console.log('buffering', e);
  }

  @autobind
  resize() {
    if ( this.state.isReady ) {
      this.setState({width: this.player.player.player.videoWidth, height: this.player.player.player.videoHeight});
    }
  }

  poster() {

    const componentStyles = [
      styles.child(),
      styles.poster(),
    ];

    if (this.props.isMobile || !this.props.posterVideoSrc) {
      return (
        <Div
          css={componentStyles}
          ref={ (node) => this.posterElement = node }
          style={{
            backgroundImage: `url(${ this.props.posterImageUrl })`,
          }}
        >
        </Div>
      );
    }

    return (
      <Div css={componentStyles} ref={ (node) => this.posterElement = node }>
        <ReactPlayer
          ref={(player: any) => { this.player = player; }}
          className={componentStyles}
          onClick={ this.props.handlePosterTap }
          url={ this.props.posterVideoSrc }
          width={ this.props.posterWidth || this.props.width }
          height={ this.props.posterHeight || this.props.height }
          volume={ 0 }
          vimeoConfig={{
            iframeParams: {
              loop: true,
              autoplay: true,
              background: true,
            },
          }}
        />
      </Div>
    );
  }

  componentWillReceiveProps(newProps: IVideoInnerProps) {
    if (newProps.seekTo !== this.props.seekTo) {
      this.player.seekTo(newProps.seekTo / this.player.getDuration());
    }
  }

  transitionPoster() {
    const startOpacity = this.props.playing ? 0 : 1;
    const endOpacity = this.props.playing ? 1 : 0;
    TweenMax.killTweensOf(this.posterElement);
    TweenMax.fromTo(this.posterElement, 0.3, {
      opacity: startOpacity,
    }, {
      opacity: endOpacity,
      onComplete: () => {
        this.setState((state) => {
          return {
            ...state,
            transitioningPoster: false,
          };
        });
      },
    });
  }

  isReady(event) {
    this.setState({isReady: true, width: this.player.player.player.videoWidth, height: this.player.player.player.videoHeight});
  }

  handlePause(event: Function) {
    if (this.props.playing && this.state.isReady) {
        this.setState((state) => {
          return {
            ...state,
            transitioningPoster: true,
          };
        }, () => {
          this.props.handleVideoPause(event);
        });
    }

  }

  handleTap(event: React.MouseEvent<HTMLDivElement>) {
    if (this.props.dragging) {
      event.preventDefault();
    } else {
      this.setState((state) => {
        return {
          ...state,
          transitioningPoster: true,
        };
      }, () => {
          this.props.handlePosterTap(event);
      });
    }
  }

  handleKeyEvent(event) {
    event.persist();
    if (this.props.dragging) {
      event.preventDefault();
    } else {
      if (event.which === 32 || event.which === 13) {
        this.setState((state) => {
          return {
            ...state,
            transitioningPoster: true,
          };
        }, () => {
            this.props.onKeyUp(event.which);
        });
      }
    }
  }

  render() {
    const rat = this.props.width / this.state.width;
    const calcWidth = String(this.props.width).indexOf('%') === -1 ? `${ this.state.width * rat }px` : this.props.width;
    const calcHeight = String(this.props.height).indexOf('%') === -1 ? `${ this.state.height * rat }px` : this.props.height;
    const componentStyles = [
      styles.wrapper,
      {
        width: calcWidth,
        height: calcHeight,
      },
    ];
    return (
      <Div
        css={styles.wrapper(this.props.roundedCorner, this.props.playing, this.props.controls, this.props.noShadow)}
        style={{
          width: this.props.autosize ? '100%' : calcWidth,
          height: calcHeight,
          top: 0,
          left: 0,
          position: this.props.autosize ? 'relative' : 'absolute',
          opacity: this.state.isReady ? 1 : 0,
        }}
        onClick={this.handleTap.bind(this)}
        onKeyUp={this.handleKeyEvent.bind(this)}
        tabIndex="0"
        >

        { ((!this.props.playing && (this.player && (this.player.getCurrentTime() === 0) || (this.player && this.player.getCurrentTime() === this.player.getDuration())))) && this.poster() }
          <ReactPlayer
            ref={(player: any) => { this.player = player; }}
            preload
            className={css(styles.child()).toString()}
            width={ calcWidth }
            height={ calcHeight } // this.props.fitHeight || this.props.height }
            url={ this.props.videoSrc }
            playing={ this.props.playing }
            onBuffer={ this.buffering }
            onReady= { this.isReady.bind(this) }
            onEnded={ this.props.handleVideoEnded }
            onPause={ this.handlePause.bind(this) }
            volume={ this.props.mute ? 0 : 1 }
            loop={ this.props.loop }
            fileConfig={{
              attributes: {
                muted: this.props.mute ? 'muted' : false,
                playsInline: this.props.isMobile && this.props.isVertical ? false : true,
                autoPlay: this.props.playing ? 'autoplay' : false,
                poster: this.props.posterImageUrl,
            }}}
            onProgress={((evt) => this.props.handlePlayback ? this.props.handlePlayback(evt, this.player) : null)}
            onDuration={(evt) => this.props.onDuration(evt)}
          />
      </Div>
    );
  }
}



