import * as React from 'react';
import * as PropTypes from 'prop-types';
import glamorous from 'glamorous';
const { Div, Ol, Li } = glamorous;

import * as styles from './AnnotationSection-styles';

export interface IAnnotationSectionProps {
    items: Array<string>;
    css?: any;
}

export const ButtonPropTypes = {
    items: PropTypes.array.isRequired,
};

export class AnnotationSection extends React.Component<IAnnotationSectionProps, void> {
  render() {
    return (
      <Div css={[styles.listWrapper(), this.props.css]}>
        <Ol css={styles.listStyle()}>
          {this.props.items.map((item, i) => {
              return <Li key={i} css={styles.listItem(i + 1)}>{item}</Li>;
          })}
        </Ol>
      </Div>
    );
  }
}



