import * as React from 'react';

export class StatefulContainer extends React.Component<any, any> {
  render() {
    return this.props.children({
      setState: this.setState.bind(this),
      state: this.state || undefined,
    });
  }
}

export const hexToRGB = (hex: string) => {
  const arrBuff = new ArrayBuffer(4);
  const vw = new DataView(arrBuff);
  vw.setUint32(0, parseInt(hex.replace('#', ''), 16), false);
  const arrByte = new Uint8Array(arrBuff);
  return `${arrByte[1]}, ${arrByte[2]}, ${arrByte[3]}`;
};

export function readCookie(name: string) {
  const result = new RegExp(`(?:^|; )${ encodeURIComponent(name) }=([^;]*)`).exec(document.cookie);

  return result ? result[1] : null;
}

export function writeCookie(name: string, value: string, days?: number, domainString?: string) {
  if (!days) {
      days = 365 * 20;
  }
  const date = new Date();
  date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
  const expires = '; expires=' + date.toUTCString();
  const domain = domainString ? '; domain=' + domainString : null;
  document.cookie = `${ name }=${ value }${ expires }${ domain }; path=/`;
}

export function removeCookie(name: string) {
  this.writeCookie(name, '', -1);
}

export function safeName(val: string) {
  return val.replace(/-/g, '_');
}

export function whereAmI() {
  if (window.location.href.indexOf('success-stories') > -1) {
    return 'success-stories';
  } else if (window.location.href.indexOf('creative-spotlight') > -1) {
    return 'creative-spotlight';
  } else if (window.location.href.indexOf('blog') > -1) {
    return 'blog';
  } else if (window.location.href.indexOf('tools') > -1) {
    return 'tools';
  } else if (window.location.href.indexOf('explore') > -1) {
    return 'explore';
  } else if (window.location.href.indexOf('measurement') > -1) {
    return 'explore';
  } else if (window.location.href.indexOf('measurement') > -1) {
    return 'audiences';
  } else if (window.location.href.indexOf('ad-products') > -1) {
    return 'ad-products';
  } else if (window.location.href.indexOf('gettingstarted') > -1) {
    return 'gettingstarted';
  } else if (window.location.href.indexOf('inspiration') > -1) {
    return 'inspiration';
  } else if (window.location.href.indexOf('') > -1) {
    return 'home';
  } else {
    return 'page';
  }
}



