import * as React from 'react';
import glamorous from 'glamorous';
const { Div, Img } = glamorous;
import * as PropTypes from 'prop-types';
const ReactPlayer = require('react-player');
import { LazyloadFade } from '../../atoms/LazyloadFade';
import { autobind } from 'core-decorators';
import{ throttle } from 'lodash';
import { Markdown } from '../../atoms/Markdown';

const deviceImage = require('./assets/mobile-device@2x.png');

import * as styles from './DeviceMobile-styles';

export interface IDeviceMobileProps {
  imageUrl: string;
  imageAlt?: string;
  title?: string;
  copy?: string;
  videoUrl?: string;
  isMobile?: boolean;
  isFocused?: boolean;
  inCarousel?: boolean;
  animating?: boolean;
  timing?: number;
  hideDevice?: boolean;
  deviceImageAlt?: string;
  modalInstance?: boolean;
  playsInline?: boolean;
}

export interface IDeviceMobileState {
  playing: boolean;
  shouldShowPoster: boolean;
  initialPlay: boolean;
}

export class DeviceMobile extends React.Component<IDeviceMobileProps, IDeviceMobileState> {

  player: any;
  container: any;
  videoContainer: any;
  videoTop: number;
  scrollThrottle: any;

  static defaultProps = {
    isFocused: true,
    playsInline: true,
  };

  state = {
    playing: this.shouldAutoPlay(),
    shouldShowPoster: !this.shouldAutoPlay(),
    initialPlay: false,
  };

  componentDidMount() {
      window.setTimeout(() => {
        this.videoTop = this.container.getBoundingClientRect().top;
        this.scrollThrottle = throttle(this.scrollCallback.bind(this), 100);
        window.addEventListener('scroll', this.scrollThrottle);
        this.scrollCallback();

        // The scrollCallback doesn't trigger playback in the modal.
        if (this.state.playing && this.player && this.props.modalInstance && this.props.isMobile) {
          this.player.player.play();
        }
      }, 100);
  }

  componentWillReceiveProps(nextProps) {
    // Stop media when it looses focus, particularly for carousels.
    if ( nextProps.isFocused !== this.props.isFocused && !nextProps.isFocused ) {
      this.setState({
        playing: false,
        initialPlay: false,
      });
    }
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.scrollCallback);
  }

  @autobind
  scrollCallback() {
    const scrollTop = Math.max(document.documentElement.scrollTop, document.body.scrollTop) + window.innerHeight - 60;
    if (this.videoContainer && (scrollTop - window.innerHeight) > (this.videoTop + this.videoContainer.offsetHeight)) {
      if (this.state.playing && this.player) {
        this.player.player.pause();
      }
    } else if (this.container && scrollTop >= this.videoTop) {
      if (this.state.playing && this.player) {
        this.player.player.play();
      }
    } else {
      if (this.state.playing && this.player) {
        this.player.player.pause();
      }
    }
  }
  onTap() {
    if ( this.props.isFocused ) {
      this.setState({playing: !this.state.playing});
    }
  }

  shouldAutoPlay() {

    const deviceOSList = ['iPhone OS 10_2', 'iPhone OS 10_1', 'iPhone OS 10_0', 'iPhone OS 9_', 'Android 5', 'Android 6', 'Android 7.0'];
    const userAgent = navigator.userAgent;

    const deviceMatch = deviceOSList.filter((OS) => {
      return userAgent.indexOf(OS) > -1;
    });
    return deviceMatch.length > 0 ? false : this.props.playsInline ? true : false;
  }

  render() {
    const {
      animating,
      timing,
      playsInline,
    } = this.props;
    const {
      playing,
      shouldShowPoster,
      initialPlay,
    } = this.state;

    return (
      <Div innerRef={(container: any) => { this.container = container; }} css={ styles.container(this.props.isFocused) }>
        <div>
          <Div css={styles.defaultStyles(this.props.inCarousel, this.props.hideDevice)} data-id="deviceMobile wrapper" tabIndex={this.props.videoUrl ? '0' : null}>
            {!this.props.hideDevice && <Img className="thisistheimage" css={ styles.device() } src={deviceImage} alt={this.props.deviceImageAlt} />}
            <LazyloadFade>
              <Div
                className={'video-wrapper'}
                innerRef={(videoContainer: any) => { this.videoContainer = videoContainer; }}
                css={ styles.deviceScreen( this.props.videoUrl, animating, timing, playing, shouldShowPoster, initialPlay, this.props.hideDevice) }
                onClick={this.onTap.bind(this)}
              >
                { !this.props.videoUrl &&
                  <Img css={styles.defaultImage()} src={this.props.imageUrl} alt={this.props.imageAlt} />
                }
                { this.props.videoUrl && !this.props.isMobile &&
                  <ReactPlayer
                    ref={(player: any) => { this.player = player; }}
                    url={ this.props.videoUrl}
                    width={ '100%' }
                    height={ '100%' }
                    volume={ 0 }
                    playing={this.state.playing}
                    loop={ playsInline ? true : false }
                    fileConfig={{
                      attributes: {
                        muted: 'muted',
                        playsInline: playsInline ? true : false,
                        autoPlay: this.shouldAutoPlay() === true ? 'autoplay' : false,
                        poster: this.props.imageUrl,
                      },
                    }}
                    vimeoConfig={{
                      iframeParams: {
                        loop: playsInline ? true : false,
                        autoplay: playsInline ? true : false,
                        background: true,
                      },
                    }}
                    onPlay={() => {
                       // Used to set poster image for mobile devices.
                      this.setState({initialPlay: true});
                    }}
                    onPause={() => {
                      if (this.props.modalInstance) {
                        this.setState({playing: false});
                      }
                    }}
                  />
                }
              </Div>
            </LazyloadFade>
          </Div>
        </div>
        { ( this.props.title || this.props.copy ) &&
          <Div css={ styles.copyLockup(this.props.inCarousel) }>
            { this.props.title &&
              <Div css={ styles.deviceTitle() }>{ this.props.title }</Div>
            }
            { this.props.copy &&
              <Div css={ styles.deviceCopy() }>
                <Markdown copy={ `${this.props.copy}` } />
              </Div>
            }
          </Div>
        }
      </Div>
    );
  }
}



